let logDebugMessages = false;

const video = document.getElementById('video');
navigator.mediaDevices.getUserMedia({ video: true })
    .then(stream => {
        video.srcObject = stream;
        // Call setup after setting the stream
        setup();
    })
    .catch(error => {
        console.error('Error accessing webcam:', error);
        debugLog('Error accessing webcam: ' + error);
    });

import { FilesetResolver, PoseLandmarker } from '@mediapipe/tasks-vision';

let poseLandmarker;
async function setup() {
    debugLog('Setting up MediaPipe...');
    
    // Wait for video metadata to be loaded
    await new Promise((resolve) => {
        if (video.readyState >= 2) {
            resolve();
        } else {
            video.addEventListener('loadedmetadata', resolve);
        }
    });

    const vision = await FilesetResolver.forVisionTasks(
        "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@latest/wasm"
    );
    poseLandmarker = await PoseLandmarker.createFromOptions(vision, {
        baseOptions: {
            modelAssetPath: 'https://storage.googleapis.com/mediapipe-models/pose_landmarker/pose_landmarker_lite/float16/1/pose_landmarker_lite.task'
        },
        runningMode: 'VIDEO',
        numPoses: 1,
        minPoseDetectionConfidence: 0.5,
        minPosePresenceConfidence: 0.5,
        minTrackingConfidence: 0.5,
        outputSegmentationMasks: false
    });
    debugLog('MediaPipe setup complete!');
    debugLog(`Video dimensions: ${video.videoWidth}x${video.videoHeight}`);
    
    // Start the render loop after setup is complete
    requestAnimationFrame(renderLoop);
}

let lastVideoTime = -1;
function renderLoop() {
    debugLog('Render loop running...');
    // Only run detection if the video is ready and has valid dimensions
    if (
        poseLandmarker &&
        video.readyState >= 2 &&
        video.videoWidth > 0 &&
        video.videoHeight > 0 &&
        video.currentTime !== lastVideoTime
    ) {
        debugLog('Calling detectForVideo...');
        const poseLandmarkerResult = poseLandmarker.detectForVideo(video, performance.now());
        debugLog('detectForVideo result: ' + JSON.stringify(poseLandmarkerResult));
        processResults(poseLandmarkerResult);
        lastVideoTime = video.currentTime;
    }
    requestAnimationFrame(renderLoop);
}

function calculateAngle(a, b, c) {
    const vectorBA = { x: a.x - b.x, y: a.y - b.y };
    const vectorBC = { x: c.x - b.x, y: c.y - b.y };
    const dotProduct = vectorBA.x * vectorBC.x + vectorBA.y * vectorBC.y;
    const magnitudeBA = Math.sqrt(vectorBA.x ** 2 + vectorBA.y ** 2);
    const magnitudeBC = Math.sqrt(vectorBC.x ** 2 + vectorBC.y ** 2);
    const angle = Math.acos(dotProduct / (magnitudeBA * magnitudeBC));
    return angle * (180 / Math.PI);
}

const canvas = document.getElementById('canvas');
const ctx = canvas.getContext('2d');
canvas.width = 640;
canvas.height = 480;

// Define the connections between landmarks for a stick figure
const POSE_CONNECTIONS = [
    // Torso
    [11, 12], // shoulders
    [11, 23], // left shoulder to left hip
    [12, 24], // right shoulder to right hip
    [23, 24], // hips

    // Left arm
    [11, 13], // left shoulder to left elbow
    [13, 15], // left elbow to left wrist

    // Right arm
    [12, 14], // right shoulder to right elbow
    [14, 16], // right elbow to right wrist

    // Left leg
    [23, 25], // left hip to left knee
    [25, 27], // left knee to left ankle

    // Right leg
    [24, 26], // right hip to right knee
    [26, 28], // right knee to right ankle
];

function drawLandmarks(landmarks) {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    
    // Draw the connecting lines
    ctx.strokeStyle = 'blue';
    ctx.lineWidth = 2;
    POSE_CONNECTIONS.forEach(([start, end]) => {
        const startPoint = landmarks[start];
        const endPoint = landmarks[end];
        if (startPoint && endPoint) {
            ctx.beginPath();
            ctx.moveTo(startPoint.x * canvas.width, startPoint.y * canvas.height);
            ctx.lineTo(endPoint.x * canvas.width, endPoint.y * canvas.height);
            ctx.stroke();
        }
    });

    // Draw the landmark points
    ctx.fillStyle = 'red';
    landmarks.forEach(landmark => {
        ctx.beginPath();
        ctx.arc(landmark.x * canvas.width, landmark.y * canvas.height, 4, 0, 2 * Math.PI);
        ctx.fill();
    });
}

let repCount = 0;
let stage = null;

function processResults(results) {
    if (results.landmarks && results.landmarks[0]) {
        const landmarks = results.landmarks[0];
        drawLandmarks(landmarks);

        // Example: Left elbow angle for bicep curls
        const leftShoulder = landmarks[11];
        const leftElbow = landmarks[13];
        const leftWrist = landmarks[15];
        const angle = calculateAngle(leftShoulder, leftElbow, leftWrist);

        // Rep counting logic for bicep curls
        if (angle < 90 && stage !== 'down') {
            stage = 'down';
            debugLog('Stage: down, Angle: ' + angle.toFixed(2));
        }
        if (angle > 160 && stage === 'down') {
            stage = 'up';
            repCount++;
            document.getElementById('repCount').innerText = `Reps: ${repCount}`;
            debugLog('Rep counted! Total: ' + repCount + ', Angle: ' + angle.toFixed(2));
        }
    }
}

function debugLog(msg) {
    if (!logDebugMessages) return;
    const debugDiv = document.getElementById('debugWindow');
    if (debugDiv) {
        debugDiv.innerText += msg + '\n';
        debugDiv.scrollTop = debugDiv.scrollHeight;
    }
}

document.getElementById('debugToggle').addEventListener('change', (event) => {
    logDebugMessages = event.target.checked;
    if (logDebugMessages) {
        debugLog("Debug logging enabled.");
    } else {
        const debugDiv = document.getElementById('debugWindow');
        if (debugDiv) {
            debugDiv.innerText = 'Debug logging disabled.\n';
        }
    }
});